@import '../variables.scss';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  margin-bottom: 40px;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 450px) {
      flex-direction: column;
    }
  }

  &__logo {
    display: flex;

    @media (max-width: 450px) {
     margin-bottom: 15px;
    }

    img {
      margin-right: 15px;
      @media (max-width: 639px) {
        display: none;
      }
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
